.profileInfoBlock {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
}

.mainPhoto {
    width: 150px;
    height: 150px;
    border-radius: 50px;
    padding: 15px;
}

.uploadImageButton {
    margin-bottom: 10px;
}

.profileInfoBlock {
    padding: 10px;
}

.profileInfo {
    text-align: left;
}
.profileInfo > * {
    margin-bottom: 10px;
}
.button {
    text-align: center;
}

.doubleClickDirection {
    margin-top: 20px;
    font-size: 15px;
    color: rgb(147, 163, 133);
}

.onDoubleClickStatus {
    font-size: 25px;
    color: rgb(52 66 30);

}

.onDoubleClickStatus:hover {
    font-weight: bolder;
    color: purple;
    cursor: pointer;
}

.contacts {
    display: flex;
    flex-direction: column;
    text-align: start;
    margin-top: 30px;
}

